import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.silk.components.animation.registerKeyframes
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { com.Karthikeya.Portfolio.pages.HomePage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.initSilkHook = { ctx ->
        ctx.theme.registerComponentStyle(com.Karthikeya.Portfolio.components.animateImage)
        ctx.theme.registerComponentStyle(com.Karthikeya.Portfolio.styles.AboutCardStyle)
        ctx.theme.registerComponentStyle(com.Karthikeya.Portfolio.styles.BackToTopStyle)
        ctx.theme.registerComponentStyle(com.Karthikeya.Portfolio.styles.InputBoxStyle)
        ctx.theme.registerComponentStyle(com.Karthikeya.Portfolio.styles.NavigationItemStyle)
        ctx.theme.registerComponentStyle(com.Karthikeya.Portfolio.styles.MyNameStyle)
        ctx.theme.registerComponentStyle(com.Karthikeya.Portfolio.styles.SocialLinkIconLink)
        ctx.theme.registerComponentStyle(com.Karthikeya.Portfolio.styles.IntroImageStyle)
        ctx.theme.registerComponentStyle(com.Karthikeya.Portfolio.styles.MainButtonStyle)
        ctx.theme.registerComponentStyle(com.Karthikeya.Portfolio.styles.ProjectSectionStyle)
        ctx.theme.registerComponentStyle(com.Karthikeya.Portfolio.styles.ServiceMDCardStyle)
        ctx.theme.registerComponentStyle(com.Karthikeya.Portfolio.styles.ServiceSMCardStyle)
        ctx.theme.registerComponentStyle(com.Karthikeya.Portfolio.styles.SkillCardStyle)
        ctx.stylesheet.registerKeyframes(com.Karthikeya.Portfolio.components.profile_animation)
        com.Karthikeya.Portfolio.updateTheme(ctx)
    }

    router.navigateTo(window.location.href.removePrefix(window.location.origin))

    // For SEO, we may bake the contents of a page in at build time. However, we will overwrite them
    // the first time we render this page with their composable, dynamic versions. Think of this as
    // poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) {
        root.removeChild(root.firstChild!!)
    }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(
            AppGlobalsLocal provides mapOf("title" to "Portfolio")
        ) { com.Karthikeya.Portfolio.MyApp {
              router.renderActivePage { renderWithDeferred { it() } }
        } }
    }
}
